const setListener = (element, type, handler) =>{
	if(!element){
		return;
	}
	element.addEventListener(type, handler);
	return () =>{
		element.removeEventListener(type, handler);
	};
}
// burger
function myBurger() {
	const burger = document.querySelector(".header-menu__burger");
	const headerRow = document.querySelector(".header-menu__list");
	const curtain = document.querySelector(".offcanvas-menu-overlay");
	setListener(burger, "click", () => {
		burger.classList.toggle("burger-active");
		headerRow.classList.toggle("burger-active");
		curtain.classList.toggle("active");
		document.body.classList.toggle("lock");
	});
	setListener(curtain, "click", () => {
		curtain.classList.remove("active");
		document.body.classList.toggle("lock");
		burger.classList.remove("burger-active");
		headerRow.classList.remove("burger-active");
	})
}
myBurger();

// content upload
const loadMoreButton = document.querySelector('.portfolio__upload');
const contentContainer = document.querySelector('.portfolio-content');
const contents = contentContainer.getElementsByClassName('portfolio-content__item');
let visibleCount = 6;

hideContents();

setListener(loadMoreButton, 'click', function() {
  visibleCount += 3;
  hideContents();
});

function hideContents() {
  for (let i = 0; i < contents.length; i++) {
    if (i < visibleCount) {
      contents[i].style.display = 'block';
    } else {
      contents[i].style.display = 'none';
    }
  }
  if (visibleCount >= contents.length) {
    loadMoreButton.style.display = 'none';
  } else {
    loadMoreButton.style.display = 'inline-block';
  }
}

// move header
function move() {
	const navLinks = document.querySelectorAll('.header-menu__list a');
	const mainSections = document.querySelectorAll('.--margin-block');
	
	window.addEventListener('scroll', function() {
	  const currentScrollPosition = window.pageYOffset;
	  for (let i = 0; i < mainSections.length; i++) {
		setListener(navLinks[i], 'click', function(event) {
			event.preventDefault();
			const sectionId = navLinks[i].getAttribute('href');
			const sectionPosition = document.querySelector(sectionId).offsetTop;
			window.scrollTo({
			  top: sectionPosition,
			  behavior: 'smooth'
			});
		 });
		 const sectionPosition = mainSections[i].offsetTop;
		 const sectionHeight = mainSections[i].offsetHeight;
		 if (currentScrollPosition >= sectionPosition && currentScrollPosition < sectionPosition + sectionHeight) {
			navLinks.forEach(function(navLink) {
			  navLink.classList.remove('active');
			});
			navLinks[i].classList.add('active');
		 }
		 if (currentScrollPosition == 0) {
			navLinks.forEach(function(navLink) {
			  navLink.classList.remove('active');
			});
		 }
	  }
	});
}
move();



function oneMove() {
	const anchors = document.querySelector('.main-screen-btn a[href*="#"]')

  anchors.addEventListener('click', function (e) {
    e.preventDefault()
    
    const blockID = anchors.getAttribute('href').substr(1)
    
    document.getElementById(blockID).scrollIntoView({
      
		behavior: 'smooth',
      block: 'start'
    })
  })
}
oneMove();

